<template>
  <div class="good">
    <div class="header"></div>
    <div class="infoWrapper">
      <div class="imgWrapper">
        <div class="cover">
          <img :src="good.img">
        </div>
        <div>
          <ul class="imgList">
            <li>
              <img v-show="good.img" :src="good.img">
            </li>
            <li>
              <img v-show="good.img1" :src="good.img1">
            </li>
            <li>
              <img v-show="good.img2" :src="good.img3">
            </li>
            <li>
              <img v-show="good.img3":src="good.img4">
            </li>

          </ul>
        </div>
      </div>
      <div class="information">
        <h2>{{good.name}}</h2>
        <ul>
            <li>
              <span>价格</span>
              <p>{{good.price}}</p>
            </li>
            <li>
              <span>描述</span>
              <p>{{good.description}}</p>
            </li>
            <li>
              <span>tel</span>
              <p>{{good.tel}}</p>
            </li>
            <li>
              <span>QQ</span>
              <p>{{good.qq}}</p>
            </li>
            <li>
              <span>交易地点</span>
              <p>{{good.place}}</p>
            </li>
            <li>
              <span>日期</span>
              <p>{{good.time}}</p>
            </li>
        </ul>
      </div>
    </div>
    <div class="description"></div>
  </div>
</template>

<script>
export default {
  created(){
     this.getParams();
     this.getGoodDetail()
  },
  watch:{
    "$route":"getGoodDetail"
  },
  data(){
    return {
     goodId:"",
     good:"",
     goodDetailUrl:"/dachuang/good"
    }
  },
  methods:{
    getParams:function(){
      console.log(this.$route.query.id);
      this.goodId=this.$route.query.id;
    },
    getGoodDetail:function(){
      console.log("获取商品详细");
      this.getParams();
      var url=this.goodDetailUrl+"?id="+this.goodId;
      this.$http.get(url).then(function(res){
        console.log(res.body);
        this.good=res.body.data;
      },function(err){
        console.log(err);
      })    
    }
  }

}
</script>

<style scoped>
  .good{
    background:rgba(0, 0, 0, 0.4);
  }
  ul{
    padding: 0;
    display: inline-block;
  }
  li{
    list-style: none;
  }
  .imgList li{
    display: inline-block;
    width: 100px;
    height: 100px;
    list-style: none;
    padding: 10px;
  }
  .imgList img{
    width: 100px;
    height: 100px;
  }
  .good{
    box-sizing:border-box;
    width: 100%;
    height: 1200px;
    padding: 50px;

  }
  .imgWrapper{
    width: 500px;
    display: inline-block;
  }
  .information{
    display: inline-block;
    flex:500px;
    height: 650px;
    vertical-align: top;
    padding:0 100px;

  }
  .infoWrapper{
    display: flex;
    padding: 20px;
    text-align: left;
   
    background: #fff;
  }
  
  .cover{
    width: 500px;
    height: 500px;
    border: 1px solid #000;
  }
  .cover img{
    width: 500px;
    height: 500px;
  }
  .information ul{
    width: 100%;

  }
  .information li{
    margin: 20px 0;
  }
  .information p{
    display: inline-block;
    width: 80%;
  }
  .information span{
    display: inline-block;
    width: 10%;
  }
</style>


